import React from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import CountryCards from '../CountryCards/CountryCards';
import SearchBar from '../SearchBar/SearchBar';
import HomeStyles from './Home.module.css'


const Home = () => {
    const countries = useSelector((state) => state.countries);

    return (
        <div>
            {/* ---------Barra superior--------- */}
            <div className={HomeStyles.conteiner}>
                <div>
                    <Link to= '/'>
                        <button className={HomeStyles.button} id= 'back'>Landing</button>
                    </Link>
                    <Link ti= '/createActivity'>
                        <button className={HomeStyles.button} id= 'create'>Create Activity</button>
                    </Link>
                </div>
                <div>
                    <SearchBar/>
                </div>
            </div>

            {/* ---------Country Cards--------- */}
            <div>
                {countries.map((el) => (
                    <CountryCards
                        id = {el.id}
                        name = {el.name}
                        flag_image = {el.flag_image}
                        continent = {el.continent}
                    />
                ))}
            </div>
        </div>
    )
}

export default Home;











